@import url(https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.CardItem {
    max-width: 350px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: rgb(243, 117, 43, 0.1);
    text-align: center;
    justify-content: center;
    margin: 0 auto;
}

.CardItem img {
    max-width: 300px;
    display: flex;
}

.imgContainer {
    height: 300px;
    width: 300px;
}

.detailsContainer {
    /* display: flex;
    flex-direction: column; */
}

.choicesBtnContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.CardItem button {
    background: rgba(16, 24, 32, 0.95);
    padding: 1em;
    margin-top: 20px;
    padding-left: 1em;
    padding-right: 1em;
    border: none;
    color: #fff;
    outline: none;
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    text-decoration: none;
    font-size: 0.75em;
    text-transform: uppercase;
    transition: 0.2s ease-out;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.CardItem button:hover {
    cursor: pointer;
    transition: 0.2s ease-out;
    background-color: rgb(243, 117, 43, 0.85);
}
.OrderPage {
    /* height: 1500px; */
    background-color: #fff;
    align-content: center;
    margin-bottom: 100px;
}

.OrderPage h1 {
    margin: 20px;
    margin-top: 150px;
    text-align: center;
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    font-size: 3.5em;
    text-transform: uppercase;
    color: #000;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.25);
}

.OrderPageContainer {
    padding: 0px 50px;
}

.OrderPageContainer h1 {
    font-size: 1.5em;
    text-align: left;
    margin: 50px 0px;
}

.FoodsGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    gap: 20px;
}
.Menu {
    position: fixed;
    top: 0;
    width: 100%;
    /* background: blue; */
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    transition: 0.6s;
    z-index: 1;
}

.Scrolled {
    background-color: rgb(16, 24, 32, 0.99);
    z-index: 1;
}

.Menu a {
    display: flex;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
}

.Menu a img {
    width: 100px;
}

.Menu button {
    background-color: transparent;
    border: none;
    display: flex;
    align-items: center;
    color: #fff;
    outline: none;
    float: right;
    padding: 20px;
    position: absolute;
    top: 5%;
    right: 0;
    cursor: pointer;
}

.Menu button img {
    width: 25px;
}

.MenuOrdering {
    position: fixed;
    top: 0;
    width: 100%;
    background: #101820;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    transition: 0.6s;
    z-index: 1;
}

.MenuOrdering a img {
    width: 100px;
}

.MenuOrdering button {
    background-color: transparent;
    border: none;
    display: flex;
    align-items: center;
    color: #fff;
    outline: none;
    padding: 20px;
    cursor: pointer;
}

.MenuOrdering button img {
    width: 25px;
}

.MenuOrdering .menu-navbar {
    display: flex;
    position: absolute;
    right: 0;
    top: 5%;
}

.buttonCheckout {
    background-color: transparent;
    border: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fff;
    outline: none;
    padding: 15px;
    cursor: pointer;
}
.buttonCheckout label {
    font-size: 0.7em;
    position: absolute;
    bottom: 0;
    background-color: #F3752B;
    border-radius: 100%;
    padding: 3px;
}

#checkout {
    padding: 0px;
    position: relative;
    top: 15%;
}
.DialogShadow {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0px;
    background-color: black;
    opacity: 0.8;
    z-index: 4;
}

.Dialog {
    width: 500px;
    background-color: #FDF1E9;
    border: 1px black;
    padding: 20px;
    position: fixed;
    top: 30%;
    z-index: 5;
    max-height: calc(100% - 100px);
    left: calc(50% - 250px);
    display: flex;
    flex-direction: column;
}

#quantity {
    width: 40px;
}

.Dialog button {
    max-width: 300px;
    display: flex;
    align-self: center;
    background: rgba(16, 24, 32, 0.95);
    padding: 1em;
    margin-top: 20px;
    padding-left: 1em;
    padding-right: 1em;
    border: none;
    color: #fff;
    outline: none;
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    text-decoration: none;
    font-size: 0.7em;
    text-transform: uppercase;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.Dialog button:disabled {
    opacity: 0.5;
    background-color: grey;
    pointer-events: none;
}

.choicesBtnContainer {
    display: flex;
    flex-direction: column;
}

.choicesBtnContainer h3 {
    padding-right: 100px;
}

.Quantity {
    display: flex;
    flex-direction: column;
}
.Toppings {
    padding: 20px 0px;
}

.ToppingsGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.Topping {
    padding: 5px 0px;
}

.ToppingsGrid input {
    cursor: pointer;
    margin-right: 10px;
}

.ToppingsGrid label {
    cursor: pointer;
}
.Sizes {
    display: flex;
    flex-direction: column;
    padding: 15px 0px;
}
.Sizes select {
    max-width: 200px;
}
.Order {
    background-color: #FDF1E9;
    height: 100vh;
    width: 500px;
    position: fixed;
    top: 80px;
    right: 0;
    transition: all 2s;
    overflow: scroll;
    z-index: 1;
}

.orderContainer {
    padding: 20px 0px;
    border-bottom: 1px solid grey;
    background-color: #FDF1E9;
}

.orderItem {
    padding: 10px 0px;
    display: grid;
    grid-template-columns: 10px 120px 20px 60px;
    justify-content: space-between;
}
.detailItem p{
    padding: 2px 0px;
}

.orderItem h4 {
    margin: 10px;
}

#removeBtn {
    cursor: pointer;
}

.orderFooter {
    display: flex;
    flex-direction: column;
}

.orderFooter button {
    background: rgba(16, 24, 32, 0.95);
    padding: 1em;
    margin: 40%;
    margin-top: 20px;
    padding-left: 1em;
    padding-right: 1em;
    border: none;
    color: #fff;
    outline: none;
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    text-decoration: none;
    font-size: 0.75em;
    text-transform: uppercase;
    transition: 0.2s ease-out;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.orderFooter button:hover {
    cursor: pointer;
    transition: 0.2s ease-out;
    background-color: rgb(243, 117, 43, 0.85);
}
.Footer {
    background: #101820;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #060a0d, #101820);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: #fff;
}

.FooterContainer {
    padding: 100px;
    display: grid;
    grid-template-columns: 0.1fr 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    justify-content: center;
    /* align-items: center; */
}

.FooterContainer div {
    text-align: center;
}

.FooterContainer h3 {
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    font-size: 1.3em;
    line-height: 36px;
    margin: 10px;
}

.FooterContainer p {
    font-family: Kanit;
    font-style: normal;
    font-weight: normal;
    font-size: 0.9em;
    line-height: 21px;
    text-align: center;
}

.FooterContainer img {
    width: 100px;
}

.FooterContainer ul {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.FooterContainer ul li {
    margin: 6px;
    font-size: 2em;
}

.FooterContainer ul a {
    color: #fff;
    transition: 0.2s ease-out;
}

.FooterContainer ul a:hover {
    cursor: pointer;
    color: rgb(243,117,43, 0.8);
}
.Btn {
    padding: 0.5em;
    padding-left: 2em;
    padding-right: 2em;
    border: 1px solid rgb(243, 117, 43);
    background-color: rgb(243, 117, 43, 0.7);
    color: #fff;
    outline: none;
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    text-decoration: none;
    font-size: 1em;
    text-transform: uppercase;
    transition: 0.2s ease-out;
}

.Btn:hover {
    cursor: pointer;
    transition: 0.2s ease-out;
    background-color: rgb(243, 117, 0.9);
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.Home {
    height: 100vh;
    background: linear-gradient(89.24deg, rgba(0, 0, 0, 0.72) 0.58%, rgba(0, 0, 0, 0.05) 124.3%), url(/pizzashop/static/media/BackgroundHome.de2ba38c.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.HomeContainer {
    padding: 190px 100px 100px 100px;
}

.Home h5 {
    font-family: Kanit;
    font-style: normal;
    font-weight: 300;
    font-size: 1em;
    line-height: 27px;
    text-transform: uppercase;
    color: #fff;
}

.Home hr {
    border: 1px solid #F3752B;
    width: 140px;
}

.Home h1 {
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    font-size: 3.5em;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
}

.BtnDiv {
    margin-top: 50px;
    margin-bottom: 50px;
}

.Home h6 {
    font-family: Kanit;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 30px;
    color: #FFFFFF;
}

.Home ul {
    display: flex;
    flex-direction: row;
    color: #fff;
    list-style: none;
}

.Home ul li a {
    font-size: 1.5em;
    margin-right: 10px;
    color: #fff;
    transition: 0.2s ease-out;
}

.Home ul li a:hover {
    color: rgb(243, 117, 43, 0.8);
}
.Cardpizza {
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.Cardpizza img {
    max-width: 300px;
    /* height: 200px; */
}

.Cardpizza h2 {
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    font-size: 1.5em;
    line-height: 36px;
    text-transform: uppercase;
    padding: 10px;
}

.Cardpizza p {
    padding: 10px;
    font-family: Kanit;
    font-style: normal;
    font-weight: 300;
    font-size: 1em;
    line-height: 27px;
    text-align: center;
}
.Poppizzas {
    display: flex;
    background: #101820;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #060a0d, #101820);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    height: 100vh;
    min-height: 900px;
}

.PoppizzasContainer {
    /* margin: 0px 100px 0px 100px; */
    padding: 100px;
}

.Poppizzas h1 {
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    font-size: 2.5em;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
    line-height: 50px;
}

.Poppizzas hr {
    border: 1px solid #F3752B;
    width: 320px;
}

.cardPizzasContainer {
    margin: 100px 0px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 50px;
    gap: 50px;
    justify-content: center;
    align-items: center;
}
.PoppizzasBtnContainer {
    display: flex;
    justify-content: center;
}
.Pizzaoftheday {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
    min-height: 550px;
    background: linear-gradient(89.24deg, rgba(0, 0, 0, 0.72) 0.58%, rgba(0, 0, 0, 0.05) 124.3%), url(/pizzashop/static/media/BackgroundPizzaOfDay.040d77c2.jpg);
    background-position: center;
    background-size: cover;
    color: #FFFFFF;
}

.Pizzaoftheday h1 {
    font-family: Kanit;
    font-style: normal;
    font-weight: bold;
    font-size: 2.5em;
    text-transform: uppercase;
    text-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
    line-height: 50px;
    margin: 20px 0px;
}

.Pizzaoftheday h2 {
    font-family: Kanit;
    font-style: normal;
    font-weight: normal;
    font-size: 1.5em;
    line-height: 36px;
    text-transform: uppercase;
    padding: 10px;
}

.Pizzaoftheday p {
    margin-bottom: 50px;
    font-family: Kanit;
    font-style: normal;
    font-weight: 300;
    font-size: 1em;
    line-height: 27px;
    text-align: center;
}
